	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Nice Try, Mama","date":"08 Apr 2007","slug":"/2007-04-08-nice-try-mama/","tags":"Y2007"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2007/04/netter-versuch-mama.jpg\" alt=\"Netter Versuch, Mama\" /></p>\n<p>MamaMaus brought home this picture and said it would be good for PapaScott. File it under McDonald's humor that McDonald's people find funny. If you've never sent a child to a German school, this is obviously a school lunch <em>Pausenbrot</em> in a different wrapping.</p>\n<p>It seems to come from an <a href=\"http://derstandard.at/?url=/?id=2464453\">ad campaign for McDonald's Austria</a>.</p>\n<p>No, we don't do this. We wrap Christopher's <em>Pausenbrot</em> in normal waxed paper like all other parents.</p>"},"next":{"fields":{"title":"I Got the Bug","date":"07 Apr 2007","slug":"/2007-04-07-i-got-the-bug-2/"}},"previous":{"fields":{"title":"Minicruise Photo Set","date":"09 Apr 2007","slug":"/2007-04-09-minicruise-photo-set/"}}},{"node":{"fields":{"title":"I Got the Bug","date":"07 Apr 2007","slug":"/2007-04-07-i-got-the-bug-2/","tags":"Y2007"},"html":"<p>After our trip this week I'm laying low with a touch of the flu. I'm even skipping biggest event of the year in Lüllau, the Easter fire. Mama and Christopher are there, though, and I'm sure they can drive out the evil spirits of winter without me.</p>"},"next":{"fields":{"title":"Back In Town","date":"05 Apr 2007","slug":"/2007-04-05-back-in-town/"}},"previous":{"fields":{"title":"Nice Try, Mama","date":"08 Apr 2007","slug":"/2007-04-08-nice-try-mama/"}}},{"node":{"fields":{"title":"Back In Town","date":"05 Apr 2007","slug":"/2007-04-05-back-in-town/","tags":"Y2007"},"html":"<p>We returned last night from our <a href=\"http://www.dfds.co.uk/DSW/EN/Travel/Shortbreaks/CityCruiseBreaks/Oslo\">\"mini cruise\"</a> from Copenhagen to Oslo, with a stop at Legoland in Billund on the way home. We'll spend Easter at home before getting back to school and work next week (although MamaMaus, true to form, is working already today).</p>\n<p>I'll also be doing some work with a Torx 8 screwdriver today. Fellow MacBook owners will know <a href=\"http://vowe.net/archives/008095.html\">exactly what that means</a>... swiching to a bigger hard disk!</p>"},"next":{"fields":{"title":"Our Restaurant in Google Maps","date":"30 Mar 2007","slug":"/2007-03-30-our-restaurant-in-google-maps/"}},"previous":{"fields":{"title":"I Got the Bug","date":"07 Apr 2007","slug":"/2007-04-07-i-got-the-bug-2/"}}}],"pathPrefix":"","first":false,"last":false,"index":143,"pageCount":1101}}

